import React from 'react';
import styled from '@emotion/styled';

import Radio from './Radio';

interface IRadioGroup {
  options: {
    field: string;
    value: string;
  }[];
  onChange: (value: string) => void;
  value: string;
}

const RadioGroup: React.FC<IRadioGroup> = ({ options, onChange, value }) => {
  return (
    <Container>
      {options.map((option) => (
        <RadioWrapper key={option.value} onClick={() => onChange(option.value)}>
          <Radio selected={option.value === value} />
          <span>{option.field}</span>
        </RadioWrapper>
      ))}
    </Container>
  );
};

export default RadioGroup;

const Container = styled.div`
  display: flex;
  margin-top: 13px;
  margin-bottom: 7px;
`;

const RadioWrapper = styled.span`
  display: flex;
  cursor: pointer;
  align-items: center;
  opacity: 0.8;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;

  & > span {
    margin: 0 10px;

    &:first-of-type {
      margin-right: 30px;
    }
  }
`;
