/* eslint-disable */
import React, { useState, useEffect } from 'react';
import styled from '@emotion/styled';
import { useHistory } from 'react-router';

import Button from '../components/Button';
import InputWithLabel from '../components/InputWithLabel';
import Modal from '../components/Modal';
import AuthServiceWrapper from '../components/AuthServiceWrapper';

import { api } from '../utils/api';
import RadioGroup from '../components/RadioGroup';
import auth from '../utils/auth';

const Register = () => {
  const history = useHistory();
  const [info, setInfo] = useState({
    email: '',
    password: '',
    rePassword: '',
    phone: '',
    birth: '',
    oldPassword: '',
    gender: 'm',
  });
  const [error, setError] = useState({
    email: false,
    password: false,
    rePassword: false,
    phone: false,
    birth: false,
    oldPassword: false,
  });
  const [isModified, setIsModified] = useState(false);
  const [checkedEmail, setCheckedEmail] = useState(false);

  const [errorMessage, setErrorMessage] = useState('');
  const [isErrorModalOpen, setIsErrorModalOpen] = useState(false);

  const handleChange = (e: any) => {
    e.persist();
    setInfo((prevState) => ({ ...prevState, [e.target.name]: e.target.value }));
  };

  const handleCheckExistsEmail = async () => {
    const {
      data: { success, alert },
    } = await api.get('/email/check', {
      params: { email: info.email },
    });
    if (!success) {
      setError((prevState) => ({ ...prevState, email: true }));
      if (alert) {
        setErrorMessage(alert);
        setIsErrorModalOpen(true);
      }
    } else {
      setError((prevState) => ({ ...prevState, email: false }));
      setCheckedEmail(true);
    }
  };

  const handleSubmit = async () => {
    const {
      data: { success, alert },
    } = await api.post('/user/modify', {
      email: info.email,
      phone: info.phone,
      birth: info.birth,
      gender: info.gender,
      old_password: info.oldPassword,
      new_password: info.password,
    });
    if (success) {
      const {
        data: {
          data: {
            user: { email },
          },
        },
      } = await api.get('/user');
      auth.setEmail(email);
      setIsModified(true);
    } else {
      if (alert) {
        setErrorMessage(alert);
        setIsErrorModalOpen(true);
      }
    }
  };

  useEffect(() => {
    if (info.rePassword && info.password !== info.rePassword) {
      setError((prevState) => ({ ...prevState, rePassword: true }));
    } else {
      setError((prevState) => ({ ...prevState, rePassword: false }));
    }
  }, [info.password, info.rePassword]);

  useEffect(() => {
    if (info.password.length < 8) {
      setError((prevState) => ({ ...prevState, password: true }));
    } else {
      setError((prevState) => ({ ...prevState, password: false }));
    }
  }, [info.password]);

  useEffect(() => {
    const fetchData = async () => {
      const {
        data: {
          data: { user },
        },
      } = await api.get('/user');
      console.log(user);
      setInfo({
        email: user.email,
        oldPassword: '',
        password: '',
        rePassword: '',
        phone: user.phone,
        birth: user.birth,
        gender: user.gender,
      });
    };
    fetchData();
  }, []);

  return (
    <>
      <AuthServiceWrapper title="프로필 수정" center={true}>
        <InputWrap>
          <InputWithLabel
            name="email"
            label="아이디(E-mail)"
            placeholder="아이디(E-mail)를 입력해주세요."
            value={info.email}
            onChange={handleChange}
            error={error.email}
            disabled={true}
            errorMessage="중복된 이메일"
          />
          <InputWithLabel
            name="oldPassword"
            label="비밀번호"
            placeholder="비밀번호를 입력해주세요."
            type="password"
            value={info.oldPassword}
            onChange={handleChange}
            error={error.oldPassword}
            errorMessage="8자리 이상으로 입력"
          />
          <InputWithLabel
            name="password"
            label="새로운 비밀번호"
            placeholder="새로운 비밀번호 입력"
            type="password"
            value={info.password}
            onChange={handleChange}
            error={error.password}
            errorMessage="8자리 이상으로 입력"
          />
          <InputWithLabel
            name="rePassword"
            label="새로운 비밀번호 확인"
            placeholder="새로운 비밀번호 확인 입력"
            type="password"
            value={info.rePassword}
            onChange={handleChange}
            error={error.rePassword}
            errorMessage="비밀번호 불일치"
          />
          <InputWithLabel
            name="phone"
            label="휴대폰 번호"
            placeholder="휴대폰 번호를 입력해주세요."
            maxLength={11}
            value={info.phone}
            onChange={handleChange}
            error={error.phone}
          />
          <InputWithLabel
            name="birth"
            label="생년월일"
            placeholder="생년월일 8자리를 입력해주세요."
            maxLength={8}
            value={info.birth}
            onChange={handleChange}
            error={error.birth}
          />
          <RadioGroup
            options={[
              { field: '남자', value: 'm' },
              { field: '여자', value: 'f' },
            ]}
            value={info.gender}
            onChange={(value: string) => setInfo({ ...info, gender: value })}
          />
        </InputWrap>
        <ButtonWrap>
          <Button styleType="blue" onClick={handleSubmit}>
            수정하기
          </Button>
        </ButtonWrap>
      </AuthServiceWrapper>
      {isModified && (
        <Modal
          open={isModified}
          onClose={() => setIsModified(false)}
          firstButtonLabel="확인"
          onClickFirstButton={() => history.goBack()}
          title="프로필 수정 완료"
          content="프로필 수정이 완료되었습니다."
        />
      )}
      <Modal
        open={isErrorModalOpen}
        onClose={() => setIsErrorModalOpen(false)}
        firstButtonLabel="확인"
        onClickFirstButton={() => setIsErrorModalOpen(false)}
        title="에러"
        content={errorMessage}
      />
    </>
  );
};

const InputWrap = styled.div`
  display: flex;
  flex-direction: column;
  margin-bottom: 70px;

  & > * {
    margin-bottom: 30px;
  }
`;

const ButtonWrap = styled.div`
  & > * {
    margin-bottom: 10px;
  }
`;

export default Register;
